<template>
	<div class="search-container">
		<div class="search-box">
			<van-search v-model.trim="data.searchVale" clearable  placeholder="请输入你要搜索的武将名称" @search="onSearch" aria-autocomplete="off"  />
		</div>
		<div class="search-result-container">
			<loading v-if="data.loading" />
			<div class="no-data" v-if="!data.loading && data.noData">
				<img src="@/assets/image/search/jg.png" alt="">
				<div class="tips">抱歉 没有找到主公想要的数据</div>
				<div class="tips">试试搜索别的武将吧</div>
			</div>
			<div class="info-container" v-if="!data.loading && !data.noData">
				<div class="header">
					<div class="header-item">返场次数</div>
					<div class="header-item">皮肤</div>
					<div class="header-item">斗地主</div>
				</div>
				
				<div class="header info">
					<div class="header-item">
						<div class="info"><span class="wj-pf">{{data.wjInfo.length}}</span>次</div>
						<div v-for="(item, index) in data.wjInfo" :key="index" class="info">{{item.date}}</div>
					</div>
					<div class="header-item">
						<div class="info"><span class="wj-pf">{{data.pfInfo.length}}</span>次</div>
						<div v-for="(item, index) in data.pfInfo" :key="index" class="info">
							<span class="wj-pf">{{item.name}}</span>
						</div>
					</div>
					<div class="header-item">
						<div class="info">出场率 <span class="success">{{data.dizhuInfo.appearRate}}</span></div>
						<div class="info">地主胜率 <span class="success">{{data.dizhuInfo.dizhuRate}}</span></div>
						<div class="info">农民胜率 <span class="success">{{data.dizhuInfo.nongminRate}}</span></div>
						<div class="info">综合胜率 <span class="success">{{data.dizhuInfo.zongheRate}}</span></div>
					</div>
				</div>
				<img :src="data.wjPic" class="wj-pic">
			</div>
			
		</div>
	</div>
</template>

<script setup>
	/* eslint-disable */
	import {reactive, onMounted} from 'vue';
	import loading from '@/components/loading/index.vue';
	import archiveData from '@/mock/archive/data';
	import wjData from '@/mock/archive/wj-data';
	import pfData from '@/mock/archive/pf-data';
	import wjObj from '@/mock/wj/data';
	const data = reactive({
		loading: true,
		noData: false,
		searchVale: '',
		wjInfo: [],
		pfInfo: [],
		dizhuInfo: {
			appearRate: '--',
			dizhuRate: '--',
			nongminRate: '--',
			zongheRate: '--'
		},
		wjPic: ''
	})
	
	const onSearch = () => {
		data.loading = true;
		data.wjInfo = [];
		data.pfInfo = [];
		data.dizhuInfo = {
			appearRate: '--',
			dizhuRate: '--',
			nongminRate: '--',
			zongheRate: '--'
		};
		console.log(data.searchVale);
		// 从归档中找出武将信息
		let wjCode;
		archiveData.forEach( (value) => {
			value.wj.forEach( (v) => {
				if(v.name === data.searchVale) {
					wjCode = v.code
				}
			})
			if(!wjCode) {
				value.xwj.forEach( (v) => {
					if(v.name === data.searchVale) {
						wjCode = v.code
					}
				})
			}
			if(!wjCode) {
				value.pf.forEach( (v) => {
					if(v.name === data.searchVale) {
						wjCode = v.code
					}
				})
			}
		})
		if(wjCode) {
			// 先找武将返场
			if(wjData[wjCode]) {
				data.wjInfo = wjData[wjCode].list;
			}
			// 再找皮肤返场
			if(pfData[wjCode]) {
				data.pfInfo = pfData[wjCode].list;
			}
		}
		
		// 再找斗地主信息
		let totalCount = 0;
		let hasData = false;
		Object.keys(wjObj).forEach( (key) => {
			totalCount += wjObj[key].landbord.total;
			if(wjObj[key].name === data.searchVale) {
				hasData = true;
				data.dizhuInfo.appearRate = Number((wjObj[key].landbord.total/totalCount).toFixed(2)*100) + '%';
				if(wjObj[key].landbord.dizhu.total > 0) {
					data.dizhuInfo.dizhuRate = Number((wjObj[key].landbord.dizhu.success/wjObj[key].landbord.dizhu.total).toFixed(2)*100) + '%';
				}
				if(wjObj[key].landbord.nongmin.total > 0) {
					data.dizhuInfo.nongminRate = Number((wjObj[key].landbord.nongmin.success/wjObj[key].landbord.nongmin.total).toFixed(2)*100) + '%';
				}
				data.dizhuInfo.zongheRate = Number((wjObj[key].landbord.success/wjObj[key].landbord.total).toFixed(2)*100) + '%';
				data.wjPic = wjObj[key].icon;
			} 
		})
		if(data.wjInfo.length == 0 && data.pfInfo.length == 0 && !hasData) {
			// 没有找到数据
			data.noData = true;
		} else {
			data.noData = false;
		}
		data.loading = false;
	}
	
	onMounted(() => {
		data.loading = false;
		data.noData = true;
	})

</script>

<style lang="less" scoped>
	.search-container {
		height: 100%;
		background-color: rgba(255, 255, 255, .8);
		border-radius: 12px;
		display: flex;
		flex-direction: column;
		.search-box{
			flex-shrink: 0;
			opacity: 0.85;
			.van-search{
				background-color: transparent;
			}
		}
		.search-result-container{
			flex-grow: 1;
			.no-data{
				padding-top: 36px;
				img{
					width: 80%;
					display: block;
					margin: auto;
				}
				.tips{
					color: #8f4b28;
					text-align: center;
					margin-top: 10px;
				}
			}
			.info-container{
				padding: 16px;
				position: relative;
				.header{
					display: flex;
					&.info{
						
					}
					.header-item{
						flex: 1;
						.info{
							padding: 4px 0;
							.wj-pf {
								color: #008c8c;
							}
							.success{
								color: #db2a2f;
								font-size: 18px;
							}
						}
					}
				}
				.wj-pic{
					position: absolute;
					display: block;
					width: 100%;
					top: 12px;
					left: 0;
					user-select: none;
					pointer-events: none;
					opacity: 0.35;
				}
			}
		}
	}
</style>